import { getInstance } from 'd2';
import arrayClean from 'd2-utilizr/lib/arrayClean';
import { getEndPointName, MAP } from '../modules/itemTypes';
import { getVisualizationId } from '../modules/item'; // Id, name

export const getIdNameFields = ({
  rename
} = {}) => ['id', "".concat(rename ? 'displayName~rename(name)' : 'name,displayName')]; // Item

export const getItemFields = () => ['dimensionItem~rename(id)', 'displayName~rename(name)', 'dimensionItemType']; // Dimension

export const getDimensionFields = ({
  withItems
}) => arrayClean(['dimension', 'legendSet[id]', 'filter', 'programStage', withItems ? "items[".concat(getItemFields().join(','), "]") : ""]); // Axis

export const getAxesFields = ({
  withItems
}) => ["columns[".concat(getDimensionFields({
  withItems
}).join(','), "]"), "rows[".concat(getDimensionFields({
  withItems
}).join(','), "]"), "filters[".concat(getDimensionFields({
  withItems
}).join(','), "]")]; // Favorite

export const getFavoriteFields = ({
  withDimensions,
  withOptions
}) => {
  return arrayClean(["".concat(getIdNameFields({
    rename: true
  }).join(',')), 'displayDescription~rename(description)', withDimensions ? "".concat(getAxesFields({
    withItems: true
  }).join(',')) : "", withOptions ? ['*', '!attributeDimensions', '!attributeValues', '!category', '!categoryDimensions', '!categoryOptionGroupSetDimensions', '!columnDimensions', '!dataDimensionItems', '!dataElementDimensions', '!dataElementGroupSetDimensions', '!filterDimensions', '!itemOrganisationUnitGroups', '!lastUpdatedBy', '!organisationUnitGroupSetDimensions', '!organisationUnitLevels', '!organisationUnits', '!programIndicatorDimensions', '!relativePeriods', '!reportParams', '!rowDimensions', '!translations', '!userOrganisationUnit', '!userOrganisationUnitChildren', '!userOrganisationUnitGrandChildren'].join(',') : '']);
};
export const getFavoritesFields = () => ["reportTable[".concat(getFavoriteFields({
  withDimensions: false
}).join(','), "]"), "chart[".concat(['type', ...getFavoriteFields({
  withDimensions: false
})].join(','), "]"), "map[".concat(getFavoriteFields({
  withDimensions: false
}).join(','), "]"), "eventReport[".concat(getFavoriteFields({
  withDimensions: false
}).join(','), "]"), "eventChart[".concat(getFavoriteFields({
  withDimensions: false
}).join(','), "]")]; // List item

export const getListItemFields = () => ["reports[".concat(['type', ...getIdNameFields({
  rename: true
})].join(','), "]"), "resources[".concat(getIdNameFields({
  rename: true
}).join(','), "]"), "users[".concat(getIdNameFields({
  rename: true
}).join(','), "]")]; // Map

export const getMapFields = () => ["".concat(getIdNameFields({
  rename: true
}).join(',')), 'user,longitude,latitude,zoom,basemap', "mapViews[".concat(getFavoriteFields({
  withDimensions: true,
  withOptions: true
}), "]")]; // Api

export const apiFetchVisualization = async item => {
  const id = getVisualizationId(item);
  const fields = item.type === MAP ? getMapFields() : getFavoriteFields({
    withDimensions: true,
    withOptions: true
  });
  const d2 = await getInstance();
  return await d2.Api.getApi().get("".concat(getEndPointName(item.type), "/").concat(id), {
    fields
  });
};