import arrayClean from 'd2-utilizr/lib/arrayClean';
import { getIdNameFields, getListItemFields, getFavoritesFields } from './metadata';
import { isViewMode } from '../components/Dashboard/dashboardModes';

const getDashboardItemsFields = () => arrayClean(['id', 'type', 'shape', 'x', 'y', 'width~rename(w)', 'height~rename(h)', 'messages', 'text', 'appKey', "".concat(getListItemFields().join(',')), "".concat(getFavoritesFields().join(','))]);

const baseDashboardFields = arrayClean(['id', 'displayName', 'displayDescription', 'favorite~rename(starred)', 'access', 'restrictFilters', 'allowedFilters', "dashboardItems[".concat(getDashboardItemsFields().join(','), "]")]);
export const viewDashboardQuery = {
  resource: 'dashboards',
  id: ({
    id
  }) => id,
  params: {
    fields: baseDashboardFields
  }
};
export const editDashboardQuery = {
  resource: 'dashboards',
  id: ({
    id
  }) => id,
  params: {
    fields: arrayClean([...baseDashboardFields, "user[".concat(getIdNameFields({
      rename: true
    }).join(','), "]"), 'name', 'description', 'created', 'lastUpdated', 'href' // needed for d2-ui-translations-dialog
    ])
  }
}; // Get more info about selected dashboard

export const apiFetchDashboard = async (dataEngine, id, mode) => {
  const query = isViewMode(mode) ? viewDashboardQuery : editDashboardQuery;

  try {
    const dashboardData = await dataEngine.query({
      dashboard: query
    }, {
      variables: {
        id
      }
    });
    return dashboardData.dashboard;
  } catch (error) {
    console.log('Error: ', error);
  }
};